.Deck--Card {
    border: 1px solid black;
    width: 250px;
    margin-top: 4px;
    margin-bottom: 4px;
    padding: 6px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
}

.Deck--Card--MostRecent {
    background-color: rgb(214, 163, 104);
}

.Deck--Card--Reshuffle {
    background-color: rgb(230, 88, 22);
}

.Deck--Card--Discard {
    border: 4px solid rgb(170, 67, 211);
}


